.ani-slidein-b {
    animation: .5s ease-out 0s 1 slideInFromBottom;
    animation-fill-mode: forwards;
    transform-origin: center;
    position: relative;
    opacity: 0;
}
.ani-slidein-l {
    animation: .5s ease-out 0s 1 slideInFromLeft;
    animation-fill-mode: forwards;
    transform-origin: center;
    opacity: 0;
}
.ani-bouncein {
    animation: .5s ease-out 0s 1 bounceIn;
    animation-fill-mode: forwards;
    opacity: 0;
}

.extend-a {
    position: relative;
    a::after {
        content: ' ';
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
    }
}

a.button {
    appearance: button;
    text-decoration: none;
}

.dev {
    outline: 2px solid rgba(131, 131, 131, 0.6);
    background-color: rgba(131, 131, 131, 0.2);
    &-blue {
        outline: 2px solid rgba(133, 133, 255, .6);
        background-color: rgba(133, 133, 255, .2);
    }
    &-red {
        outline: 2px solid rgba(255, 133, 133, 0.6);
        background-color: rgba(255, 133, 133, 0.2);
    }
    &-green {
        outline: 2px solid rgba(133, 255, 143, 0.6);
        background-color: rgba(133, 255, 143, 0.2);
    }
    &-yellow {
        outline: 2px solid rgba(255, 255, 133, 0.6);
        background-color: rgba(255, 255, 143, 0.2);
    }
}

.details .icon:hover {
    @apply bg-emerald-600;
}




