.gradient-overlay {
    &::after {
        content: '';
        background: linear-gradient(180deg, rgba(255, 255, 255, 0) 50%, rgba(0, 0, 0, 0.6) 100%);
        @apply inset-0 absolute block z-0;
    }
}
.gradient-overlay-2{
    &::after {
        content: '';
        background: linear-gradient(180deg, rgba(0, 0, 0, 0.3) 50%, rgba(255, 255, 255, 0) 100%);
        @apply inset-0 absolute block z-0;
    }
}
.gradient-overlay-3 {
    &::after {
        content: '';
        background: linear-gradient(180deg, rgba(255, 255, 255, .3) 50%, rgba(0, 0, 0, 0.6) 100%);
        @apply inset-0 absolute block z-0;
    }
}
.gradient-overlay-4 {
    &::after {
        content: '';
        background: linear-gradient(180deg, rgba(255, 255, 255, 0) 50%, rgba(0, 0, 0, 0.2) 100%);
        @apply inset-0 absolute block z-0;
    }
}
.gradient-overlay-5 {
    &::after {
        content: '';
        background: linear-gradient(358.12deg, #000000 1.85%, rgba(0, 0, 0, 0) 22.11%, rgba(0, 0, 0, 0) 59.85%, rgba(0, 0, 0, 0.5) 77.6%);
        @apply inset-0 absolute block z-0;
    }
}

.soft-gradient {
