._tab-nav a {
    transition: color .2s ease-out, border-color .4s ease-out;
}
._tab-nav a.checked {
    @apply text-gray-700 border-gray-700;
}

._tab-content {
    transition: opacity .4s ease-out;
    height: 220px;
    overflow-y: auto;
    @include mq(md) {
        height: 286px;
    }
}

._configurator-3d {
    width: 100%;
    height: 0;
    padding-top: 42.86%;
    position: relative;
    img {
        width: 300%;
        height: 100%;
        object-fit: cover;
        object-position: 0% 50%;
        animation: 12s ease-out 0s 1 config-demo-3d;
        animation-iteration-count: infinite;
        animation-timing-function: step-end; 
    }
}

._toggleswitch {
    span[role="checkbox"] {
        &.on {
            @apply bg-emerald-500;
            & > span {
                @apply translate-x-5;
            }
        }
        &.off {
            @apply bg-gray-200;
            & > span {
                @apply translate-x-0;
            }
        }
    }
}
._toggle-extras {
    &.on {
        @apply  text-gray-700;
        .switch {
            @apply bg-emerald-500;
            & > span {
                @apply translate-x-5;
            }
        }
    }
    &.off {
        @apply  text-gray-400;
        .switch {
            @apply bg-gray-200;
            & > span {
                @apply translate-x-0;
            }
        }
    }
}
._radio-extras {
    &.on {
        @apply  text-gray-700;
        .radio {
            & > span {
                @apply scale-100;
            }
        }
    }
    &.off {
        @apply  text-gray-400;
        .radio {
            & > span {
                @apply scale-0;
            }
        }
    }
}
._scrollfade-emerald {
    position: relative;
    &::after {
        content: ' '; 
        display: block;
        position: absolute;
        right: 0;
        left: 0;
        bottom: 0;
        height: 32px;
        background: linear-gradient(0deg, rgba(10,207,131,1) 0%, rgba(10,207,131,0) 100%);
        pointer-events: none;
    }
}

