._pin-down::after {
    content: ' ';
    display: block;
    background-image: url('../img/pin.svg');
    background-position: center;
    background-repeat: no-repeat;
    width: 22px;
    height: 90px;
    position: absolute;
    bottom: -98px;
    left: 0;
    right: 0;
    margin: 0 auto;
    @include mq(0,lg) {
        display: none;
    }
}
._pin-up::after {
    @extend ._pin-down::after;
    bottom: initial;
    top: -98px;
    transform: rotate(180deg);
    @include mq(0,lg) {
        display: none;
    }
}

