        animation-delay: .3s;
    }
}
._card-car__controlls {
    &>div {
        transform: translateZ(-1400px);
        opacity: 0;
    }
    &>div._visible {
        animation: slide-in-fwd-center .4s cubic-bezier(0.230, 1.000, 0.320, 1.000) both;
    }
    &>div._visible + div._visible {
        animation-delay: .2s;
    }
}
._card-sevice._visible {
    .icon {
        animation: slide-in-fwd-center .4s cubic-bezier(0.230, 1.000, 0.320, 1.000) both;
    }
}
@include mq(md) {
    ._card-sevice._visible + ._card-sevice._visible {
        .icon {
            animation-delay: .2s;
        }
    }
    ._card-sevice._visible + ._card-sevice._visible + ._card-sevice._visible {
        .icon {
            animation-delay: .4s;
        }
    }
}
._card-step._visible {
    animation: slide-in-fwd-center .4s cubic-bezier(0.230, 1.000, 0.320, 1.000) both;
}
@include mq(md) {
    ._card-step._visible:nth-child(3) {
        animation-delay: .2s; 
    }
    ._card-step._visible:nth-child(4) {
        animation-delay: .4s; 
    }
    ._card-step._visible:nth-child(5) {
        animation-delay: .6s; 
    }
    ._card-step._visible:nth-child(6) {
        animation-delay: .8s; 
    }
    ._card-step._visible:nth-child(7) {
        animation-delay: 1s; 
    }
}
._io-1, ._io-2 {
    opacity: 0; 
    transition: opacity .4s ease-in;
} 
._io-1._visible, ._io-2._visible {
    opacity: 1;
}
._io-1 {
    transition-delay: .2s;
}
._io-2 {
    transition-delay: .4s;
} 
