@keyframes slideInFromLeft {
    0% {
        left: -100vw;
        opacity: 1;
    }

    100% {
        left: 0;
        opacity: 1;
    }
}
@keyframes slideInFromBottom {
    0% {
        bottom: -100vh;
        opacity: 1;
    }

    100% {
        bottom: 0;
        opacity: 1; 
    }
}


@keyframes popup {
    0% {
        transform: scale(1);
        opacity: 0;
    }

    40% {
        transform: scale(1.3);
        opacity: .5;
    }

    50% {
        transform: scale(1.1);
        opacity: 1;
    }

    60% {
        transform: scale(1.1);
    }

    70% {
        transform: scale(1);
    }

    80% {
        transform: scale(1.1);
    }

    90% {
        transform: scale(1);
    }
}

@keyframes jello {
    from,
    11.1%,
    to {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
    }

    22.2% {
        -webkit-transform: skewX(-12.5deg) skewY(-12.5deg);
        transform: skewX(-12.5deg) skewY(-12.5deg);
    }

    33.3% {
        -webkit-transform: skewX(6.25deg) skewY(6.25deg);
        transform: skewX(6.25deg) skewY(6.25deg);
    }

    44.4% {
        -webkit-transform: skewX(-3.125deg) skewY(-3.125deg);
        transform: skewX(-3.125deg) skewY(-3.125deg);
    }

    55.5% {
        -webkit-transform: skewX(1.5625deg) skewY(1.5625deg);
        transform: skewX(1.5625deg) skewY(1.5625deg);
    }

    66.6% {
        -webkit-transform: skewX(-0.78125deg) skewY(-0.78125deg);
        transform: skewX(-0.78125deg) skewY(-0.78125deg);
    }

    77.7% {
        -webkit-transform: skewX(0.390625deg) skewY(0.390625deg);
        transform: skewX(0.390625deg) skewY(0.390625deg);
    }

    88.8% {
        -webkit-transform: skewX(-0.1953125deg) skewY(-0.1953125deg);
        transform: skewX(-0.1953125deg) skewY(-0.1953125deg);
    }
}


@keyframes lightSpeedInLeft {
    from {
        -webkit-transform: translate3d(-100%, 0, 0) skewX(30deg);
        transform: translate3d(-100%, 0, 0) skewX(30deg);
        opacity: 0;
    }

    60% {
        -webkit-transform: skewX(-20deg);
        transform: skewX(-20deg);
        opacity: 1;
    }

    80% {
        -webkit-transform: skewX(5deg);
        transform: skewX(5deg);
    }

    to {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
    }
}

@keyframes bounceIn {
    from,
    20%,
    40%,
    60%,
    80%,
    to {
        animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    }

    0% {
        opacity: 0;
        transform: scale3d(0.3, 0.3, 0.3);
    }

    20% {
        transform: scale3d(1.1, 1.1, 1.1);
    }

    40% {
        transform: scale3d(0.9, 0.9, 0.9);
    }

    60% {
        opacity: 1;
        transform: scale3d(1.03, 1.03, 1.03);
    }

    80% {
        transform: scale3d(0.97, 0.97, 0.97);
    }

    to {
        opacity: 1;
        transform: scale3d(1, 1, 1);
    }
}

@keyframes config-demo-3d {
    0% {
        object-position: 0% center;
    }
    33% {
        object-position: 50% center;
    }
    66% {
        object-position: 100% center;
    }
    100% {
        object-position: 0% center;
    }
}




@keyframes slide-right {
    0% {
        transform: translateX(0);
    }

    100% {
        transform: translateX(8px);
    }
}
// animation: slide-right 0.5s cubic-bezier(0.230, 1.000, 0.320, 1.000) infinite alternate backwards;

@keyframes slide-in-bck-bottom {
    0% {
        transform: translateZ(1700px) translateY(300px);
        opacity: 0;
    }

    100% {
        transform: translateZ(0) translateY(0);
        opacity: 1;
    }
}
// animation: slide-in-bck-bottom 0.6s cubic-bezier(0.250, 0.460, 0.450, 0.940) 0.5s forwards;

@keyframes slide-in-left {
    0% {
      transform: translateX(-1000px);
      opacity: 0;
    }
    100% {
      transform: translateX(0);
      opacity: 1;
    }
  }
//   animation: slide-in-left 0.7s cubic-bezier(0.075, 0.820, 0.165, 1.000) both;

@keyframes slide-out-left {
    0% {
      transform: translateX(0);
      opacity: 1;
    }
    100% {
      transform: translateX(-1000px);
      opacity: 0;
    }
  }
//   animation: slide-out-left 0.5s cubic-bezier(0.550, 0.055, 0.675, 0.190) both;

@keyframes slide-in-bottom {
    0% {
      transform: translateY(1000px);
      opacity: 0;
      pointer-events: none;
      z-index: -20;
    }
    33% {
        opacity: 0;
    }
    100% {
      transform: translateY(0);
      opacity: 1;
      pointer-events: all;
      z-index: initial;
    }
  }
//   animation: slide-in-bottom 0.5s cubic-bezier(0.075, 0.820, 0.165, 1.000) both;

@keyframes slide-out-bottom {
    0% {
      transform: translateY(0);
      opacity: 1;
    }
    50% {
        opacity: 0;
    }
    100% {
      transform: translateY(1000px);
      opacity: 0;
      z-index: -20;
      pointer-events: none;
    }
  }
//   animation: slide-out-bottom 0.5s cubic-bezier(0.600, 0.040, 0.980, 0.335) both;


@keyframes slide-in-elliptic-bottom-fwd {
    0% {
        transform: translateY(600px) rotateX(30deg) scale(0);
        transform-origin: 50% 100%;
        opacity: 0;
    }

    100% {
        transform: translateY(0) rotateX(0) scale(1);
        transform-origin: 50% -1400px;
        opacity: 1;
    }
}
// animation: slide-in-elliptic-bottom-fwd 0.7s cubic-bezier(0.645, 0.045, 0.355, 1.000) both;

@keyframes slide-in-fwd-center {
    0% {
        transform: perspective(1000px) translateZ(-1400px);
      opacity: 0;
    }
    100% {
      transform: perspective(1000px) translateZ(0);
      opacity: 1;
    }
  } 
//   animation: slide-in-fwd-center 0.4s cubic-bezier(0.230, 1.000, 0.320, 1.000) both;